body{
    margin: 0;
    padding:0;
}
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
/* common styles */
.glass{
    background: rgba(255, 255, 255, 0.3);
    /* border: 1px solid rgba(255, 255, 255, 0.7); */
    backdrop-filter: blur(100px);
}
.container{
    margin:0 50px;
}
.button{
    padding:5px 15px;
    border-radius: 24px;
}
a{
    text-decoration: none;
    color:black;
}
/* [Home Page] header */
.header{
    height:100vh;
    width:100vw;
    max-width:100%;
    max-height:100%;
    overflow: hidden;
    /* background-color: blueviolet; */
}
.header-container{
    height:100vh;
    width:100vw;
}
.navbar{
    font-size: 25px;
    display: flex;
    justify-content: space-between;
    padding-top:7px;
}
.navbar .navbar-header{
    padding:10px 0;
    font-size: 30px;
}
.navbar .navbar-link-container{
    padding:10px 0;
}
.navbar .navbar-link-container .navbar-link{
    margin-left:20px;
}
.navbar .navbar-link-container .special-nav-link{
    background-color: #903D01;
    color:#fff;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
}
.navbar .navbar-link-container .navbar-link span{
    padding-left:10px;
}
.navbar .navbar-link-container .navbar-link .icon{
    height:20px;
    width:auto;

}
.hero-container{
    height:calc(85vh - 50px);
    display: flex;
    /* justify-content: center; */
}
.hero-container .hero-text{
    width:50%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.hero-container .hero-text .hero-text-header{
    color:#903D01;
    font-size:50px;
    margin-bottom:8px;
}
.hero-container .hero-text .hero-text-content{
    /* color:#903D01; */
    font-size:40px;
    margin-bottom:18px;
}
.hero-container .hero-text .ghost-buttons .full-button{
    background-color:#903D01;
    font-size:25px;
    color:#fff;
    border:1px solid #903D01;
    display: inline-flex;
    align-items: center;
}
.hero-container .hero-text .ghost-buttons .full-button .icon{
    height:20px;
    width:auto;
    padding-right:10px;
}
.hero-container .hero-text .ghost-buttons .half-button{
    border:1px solid #903D01;
    font-size: 25px;
    color:#903D01;
    margin-left:10px;
    transition: all .1s ease-out;
}
.hero-container .hero-text .ghost-buttons .half-button:hover{
    /* border:1px solid #903D01; */
    /* font-size: 25px; */
    background-color:#903D01;
    color:#fff;
    /* margin-left:10px; */
}
.hero-container .hero-image{
    width:50%;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/* [homepage] section  */
.section{
    /* height:100vh;
    width:100vw; */
    /* overflow: hidden; */
}
.section .features{
    display: flex;
    justify-content:space-between;
    flex-wrap: wrap;
    position: relative;
    top:-70px;
}
.section .features .feature-box{
    margin-top:20px;
    padding:20px;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width:calc(100% / 4 - 15px);
    border:1px solid rgba(255, 255, 255, 0.5);
    /* margin-right:15px; */
}
.section .features .feature-box img{
    height:100px;
    width:auto;
    margin-bottom:20px;
}
.section .features .feature-box span{
    font-size: 25px;
    line-height: 40px;
}
.section .call-to-action-form{
    padding:20px;
    border-radius: 14px;
    position: relative;
    top:-35px;
}
.field-container{
    display: flex;
    justify-content:center;
    margin-top:25px;
    height:45px;
}
.field-container img{
    height: 100%;
    max-height:100%;
    width:auto;
    padding:10px 20px;
    background-color: #FFFFFF;
    text-align: center;
    border-radius: 14px 0 0 14px;
    font-size: 20px;
    border: 2px solid grey;
    border-right:none;
}
.section .call-to-action-form form .field-container input{
    width:25%;
}
.field-container input{
    padding:10px 10px 10px 20px;
    border-radius: 0 14px 14px 0;
    border: none;
    /* width:25%; */
    font-size:17px;
    height:100%;
    max-height:100%;
    border:2px solid grey;
    transition: all .1s ease-out;
}
.field-container input:focus{
    border:2px solid #903D01;
}
.section .call-to-action-form form textarea{
    height:100px;
    width:calc(25% + 70px);
    display: block;
    margin-top:25px;
    border-radius: 14px;
    border:none;
    padding:10px 10px 10px 20px;
    /* text-align: center; */
    font-size:17px;
    border:2px solid grey;
    transition: all .1s ease-out;
    resize: none;
}
.section .call-to-action-form form textarea:focus{
    border:2px solid #903D01;
}
.section .call-to-action-form form .submit-btn
{
    padding:7px 30px;
    border:none;
    background-color: #903D01;
    color:#fff;
    margin-top:20px;
    /* width:calc(25% + 70px); */
    font-size: 22px;
    cursor: pointer;
}
.footer{
    /* background-color: #903D01;
    color:#fff; */
    /* color:#fff; */
    /* margin-top:20px; */
    /* border-radius: 14px; */
    padding:10px 20px;
    text-align: center;
    font-size: 20px;
}
/* [Modal.js] Login Form */
.floating-login-form{
    position:absolute;
    right:30px;
    top:60px;
    z-index:5;
    padding:0 20px 20px 20px;
    border-radius: 14px;
}
.floating-login-form input{
    width:250px;
}
.floating-login-form .submit-btn{
    background-color: #903D01;
    color:#fff;
    padding:10px 20px;
    border-radius: 24px;
    margin-top:20px;
    cursor: pointer;
    width:100%;
    font-size:20px;
    border: none;
}
.floating-login-form .submit-btn:hover{
    background-color: #000000;
}

.floating-login-form .link{
    display: block;
    margin-top:10px;
    color:#903D01;
    text-align: center;
    font-size:20px;
    cursor: pointer;
    transition: all .2s ease-out;
}
.floating-login-form .link:hover{
    color: #000000;
}
.floating-login-form .link .arrow{
    font-size:30px;
}
.floating-login-form .back-btn{
    height:40px;
    width:40px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color:#ffffff;
    position:absolute;
    left:-50px;
    top:0;
    text-align: center;
    font-size:30px;
    cursor: pointer;
}
.floating-login-form .back-btn:hover{
    background-color: rgba(0, 0, 0, 1);
}
 /* signup form */
 .form-container{
    position:absolute;
    bottom:20px;
    top:20px;
    left:20px;
    right:20px;
    padding:20px;
    border-radius: 14px;
 }
 .breadcum{
     background-color: #fff;
     padding:10px;
     display: flex;
     justify-content: center;
     align-items: center;
     border-radius: 14px;
 }
 .breadcum .breadcum-text {
    font-size:1.5rem;
    padding:0 20px;
    color:grey;
 }
 .breadcum .breadcum-icon{
    font-size:1.6rem;
 }
 .breadcum .active{
    color:black;
 }
 .form-content{
     margin-top:20px;
     border-radius: 14px;
     background-color: #fff;
     padding:20px;
 }
 .form-content form{
     display: flex;
     flex-direction: column;
     /* justify-content: center; */
     align-items: center;
     /* width:50%; */
 }
 .form-content form input{
     margin-top:20px;
     padding:10px 20px;
     border-radius: 14px;
     border:2px solid grey;
     font-size: 1.2rem;
     width:40%;
     /* text-align: center; */
 }
.form-content-buttons{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
.form-content-buttons button{
    padding:10px 20px;
    border:none;
    border-radius: 24px;
    background-color: rgb(160, 158, 158);
    font-size: 1rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    /* justify-content: center; */
}
.form-content-buttons .disable{
    cursor:not-allowed;
    background-color: rgb(214, 214, 214);
}
.form-content-buttons button span{
    padding:0 5px;
    position:relative;
    top:-2px;
}
.branches{
    background-color: #fff;
    border-radius: 14px;
    padding:10px;
    width:calc(100% / 2 - 20px);
    margin:10px;
    display: inline-block;
}
.branches p{
    padding:5px 0;
}