.dashborad-header{
    height:100vh;
    width:100vw;
}
.dashboard-container{
    height:calc(100% - 40px);
    width: calc (100% -40px);
    position:absolute;
    top:20px;
    bottom:20px;
    left:20px;
    right:20px;
    border-radius: 14px;
    display: flex;
}
.dashboard-sidenav{
    display: inline-flex;
    /* display:flex; */
    flex-direction: column;
    justify-content: space-between;
    height:100%;
    border-radius: 14px;
    padding:20px 20px;
}
.dashboard-sidenav .dashboard-sidenav-header .username{
    font-size:25px;
    text-align: center;
}
.dashboard-sidenav .dashboard-sidenav-header .branch{
    margin-top: 3px;
    font-size:17px;
    text-align: center;
    color:grey;
}
.divider{
    height:1px;
    background-color:rgba(0, 0, 0, 0.2);
    width:100%;
    margin-top:3px;
}
.dashboard-sidenav-links-icon{
    margin-top:20px;
    display: flex;
    justify-content: space-between;
    box-sizing: content-box;
    padding:7px 20px;
    border:2px solid black;
    border-radius: 24px;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.2);

}
.dashboard-sidenav-links-icon:hover{
    background-color: #ffffff;
    border:2px solid black;
}
.dashboard-sidenav-links-icon-text{
    font-size:20px;
}
.dashboard-sidenav-links-icon-icon{
    height:25px;
    width:auto;
    margin-left:40px;
}
.special-icon{
    height:30px;
    width:auto;
}
.active{
    background-color: #ffffff;
    /* padding:5px 15px; */
    /* border-radius: 24px; */
    /* border:2px solid black; */
}
.dashborad-footer img{
    margin-top:15px;
    height:30px;
    width:auto;
}
.dashboard-main{
    height:100%;
    max-height: 100%;
    overflow: auto;
    border-radius: 14px;
    flex-grow: 1;
    margin-left: 20px;
    padding:20px;
}
/* Bill payments */
.box-container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.bill-heading{
    /* text-align: center; */
    color:grey;
    margin-bottom:20px;
    text-align:center;
}
.bill-box{
    background-color: #FFFFFF;
    border-radius: 14px;
    padding:15px;
    display: inline-flex;
    align-items: unset;
    flex-direction: column;
    width:calc(100% / 3 - 10px);
    margin-right:20px;
    margin-bottom:20px;
    /* justify-content: space-between; */
}
.bill-box .price{
text-align: center;
font-size: 1.5rem;
}
.bill-box .days-info{
    color:rgba(0, 0, 0, 0.7);
    font-size: 18px;
    text-align: center;
    margin-top:15px;
}
.date-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 25px;
}
.date-info span{
    color:grey;
    font-size: 15px;
    font-weight: 500;
    display: flex;
    align-items: center;
}
.date-info span img{
    height:20px;
    width:auto;
    padding-right:10px;
}
.pay-link{
    background-color: rgba(0, 0, 0, 0.7);
    /* background-color: grey; */
    color:#fff;
    padding:3px 10px;
    border-radius: 24px;
    font-size:18px;
    display: flex;
    justify-content:space-around;
    align-items: center;
    cursor: pointer;

}
.pay-link img {
    height:10px;
    width:10px;
    width:auto;
    padding-left:10px;
}
.paid-link{
    padding:3px 15px;
    font-size:18px;
    display: flex;
    align-items: center;

}
.paid-link img {
    height:15px;
    width:auto;
    padding-left:5px;
}
/* bill modal styling */
.card-details{
    display: flex;
    flex-direction: column;
}
.card-details h4{
    margin-bottom:10px;
    text-align: center;
}
.card-details span{
    margin:5px 0;
}
.card-details button{
    border:none;
    background-color:rgba(0, 0, 0, 0.9);
    color:#ffffff;
    padding:5px 10px;
    border-radius: 24px;
    margin-top:10px;
    cursor: pointer;
}
.backdrop{
    background-color: rgba(0,0,0,0.72);
    position:fixed;
    top:0;
    bottom: 0;
    left:0;
    right:0;
    cursor:pointer;
    z-index: 1;
    /* border-radius: 14px; */
}

.modal-content{
    position:absolute;
    /* top:10%; */
    /* margin: 0 auto; */
    /* max-width: 80%; */
    background-color: #ffffff;
    padding:10px 20px;
    border-radius: 14px;
    cursor:initial;
    max-height: 80%;
    overflow: auto;
    z-index: 2;
}
.big{
    padding:10px;
    top:10%;
    left:10%;
    width:80%;
}
.small{
    /* min-width:50%; */
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    max-width:80%;
    margin:0 auto;
    padding:10px 50px 10px 10px;
}
.modal-content .back-btn{
    height:40px;
    width:40px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color:#ffffff;
    padding:1px 10px;
    position:absolute;
    top:2px;
    right:2px;
    text-align: center;
    font-size:30px;
    cursor: pointer;
}
.modal-content .back-btn:hover{
    background-color: rgba(0, 0, 0, 0.9);
}
/* payments.js styling */
.payment-container{
    background-color: #fff;
    padding:20px;
    border-radius: 14px;
}
.modal-form form input{
    transition: all .3 ease-in-out;
    width:100%;
}
.modal-form form input:focus{
    border:2px solid rgb(245, 47, 47);
}
.modal-form form .submit-btn{
    background-color: rgba(0, 0, 0, 0.9);
    color:#ffffff;
    border-radius: 24px;
    border:none;
    cursor: pointer;
}
/* settings.js styling */
.user-name-field,.password-field{
    background-color: #fff;
    padding:20px;
    margin-top:20px;
    border-radius: 14px;
}
.user-name-field p,.password-field h3{
    text-align:center;
}
.user-name-field form input,.password-field form input{
    border:2px solid grey;
    display: block;
    padding:5px 10px;
    border-radius: 14px;
    font-size: 1rem;
    margin-top:10px;
    text-align: center;
}
.user-name-field form .submit-btn,.password-field form .submit-btn{
    background-color: rgba(0, 0, 0, 0.9);
    color:#FFFFFF;
    border:none;
    border-radius: 24px;
    padding:5px 20px;
    cursor: pointer;
}
.subscription-data{
    display: flex;
    flex-wrap: wrap;
}
.image-container{
    border: 1px solid rgb(194, 194, 194);
    background-color: #fff;
    /* color:#fff; */
    /* box-shadow: 5px 5px 5px rgba(255, 255, 255, 0.5); */
    border-radius: 14px;
    width:calc(100% / 3 - 20px);
    /* height: 300px; */
    margin:10px;
    /* display: flex;
    flex-direction: column; */

    /* justify-content: space-between; */
    /* align-items: center; */
    /* position: relative; */
}
.imge-data{
    display: flex;
    justify-content: space-between ;
    align-items: center;
    padding:10px;
}

.image-container img{
    height:150px;
    width:100%;
    /* text-align: center; */
    flex-grow: 1;
    max-width: 100%;
    max-height: 100%;
    border-radius: 14px 14px 0 0;
    border-bottom: 1px solid rgb(194, 194, 194);
}
.image-container .title{
    font-size: 1.2rem;
    /* text-align: center; */
    /* margin-top: 5px; */
}
.image-container .vertical-divider{
    font-size:1.5rem;
    color:rgb(131, 131, 131);
    padding:0 10px;
}
.image-container button{
    background-color: rgb(255, 91, 91);
    border:none;
    padding:5px 10px;
    border-radius: 24px;
    color:#fff;
    font-size: 1rem;
    cursor: pointer;
}
.image-container .price{
    color:rgb(73, 73, 73);
}
.del-btn{
    background-color: rgb(245, 47, 47);
    color:white;
    border: none;
    padding:5px 10px;
    border-radius: 24px;
    cursor: pointer;

} 
.add-btn{
    background-color:rgb(37, 37, 255);
    color:#fff;
    border: none;
    padding:5px 20px;
    border-radius: 24px;
    font-size: 1.1rem;
    margin:10px 0;
    cursor: pointer;
}
.disabled{
    background-color:grey;
    color:black;
    cursor:not-allowed
}
/* Daily News paper styling */
table{
    background-color: #fff;
    width: 100%;
    padding:10px;
    border-radius: 14px;
}
table th,table td{
    border: 1px solid #ddd;
    padding: 8px;
}
table tr:nth-child(even){background-color: #f2f2f2;}
table tr:hover {background-color: #ddd;}

table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}